* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none;
  outline: none;
  text-decoration: none;
  font-size: 1.2vw;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  scroll-behavior: smooth;
}

button {
  cursor: pointer;
}

#root {

  background: url(../public/assets/background.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
}

img {
  height: 1.5em;
}

.main {
  background-color: rgb(17, 17, 17, 0.5);
  padding: 20px 40px;
  backdrop-filter: blur(10px);
  display: flex;
  flex-direction: column;
  gap: 8rem;
  height: 100%;
}

header {
  display: flex;
  align-items: center;
}


.logo {
  font-size: 1.4em;
  font-weight: bolder;
}

.logo span {
  font-size: inherit;
  font-weight: inherit;
}

.header-nav {
  flex: 1 0 0;
  justify-content: flex-end;
  display: flex;
  align-self: stretch;
  gap: 5rem;
}

.nav-link a {
  font-size: 1.4em;
}

main {
  display: flex;
  gap: 1rem;
  justify-content: space-between;
}

.main-text {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.title {
  color: rgba(250, 251, 255, 0.65);
  font-size: 1.2rem;
}

.subtitle {
  font-size: 1.5em;
  max-width: 60vw;
  margin-bottom: 1rem;
  line-height: 2rem
}

button {
  align-self: flex-start;
  border-radius: 0.2rem;
  background: #292929;
  padding: 1.25rem 2.5rem;
}

.main-nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
}

.nav__social {
  display: flex;
  width: 4em;
  height: 4em;
}

.nav__social a {
  border-radius: 100%;
  background: #292929;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}


.section__title {
  font-size: 1.5em;
}

hr {
  background-color: #fff;
  height: 1px;
  margin-bottom: 2rem;
}

.section-projects {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

footer {
  display: flex;
  gap: 1rem;
}

footer .logo {
  display: flex;
  align-items: center;
}

footer .logo a {
  font-size: 1em;
}

.footer-nav {
  display: flex;
  flex: 1 0 0;
  justify-content: center;
  gap: 2rem;
}

.about-link {
  align-self: stretch;
  display: flex;
  justify-content: center;
  padding: 0;
}

.about-link a {
  display: flex;
  flex: 1 0 0;
  padding: 1.25rem 2.5rem;
  justify-content: center;
  gap: 1rem;
}

.about {
  flex-direction: row;
}

.about-descr {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  gap: 20px;
}

.about-descr h2 {
  font-size: 1.3em;
}

.about-descr li {
  list-style-type: none;
}

.about-descr__elem {
  display: flex;
  flex-direction: column;
  flex: 1 0 0;
  gap: 10px;
}

.about-descr__elem ul {
  display: flex;
  flex-direction: column;
  gap: 7px;
  font-weight: normal;
}

.about-image {
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;

}

.about-image img {
  width: 100%;
  height: auto;
}

.clone {
  display: none;
}


@media screen and (max-width: 48em) {
  * {
    font-size: 1.4vw;
  }
}

@media screen and (max-width: 27em) {
  * {
    font-size: 3vw;
  }

  .main {
    padding: 30px 20px;
  }

  .header-nav {
    gap: 2.5rem;
  }
}

@media screen and (max-width: 20em) {
  * {
    font-size: 3.2vw;
  }

  .header-nav {
    gap: 2.5rem;
  }

  .about {
    flex-direction: column;

  }

  .about-descr {
    width: 100%;
  }

  .about-image {
    display: none;
  }

  .clone {
    display: flex;
    width: 100%;
  }

  .logo span {
    display: none;
  }

  /* test */
}